* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #2C3E50;
  font-family: 'Lato', sans-serif;
}



header {
  padding: 50px 15px 0;
  text-align: center;
}

header h1 {
  color: #FFF;
  font-style: 48px;
}

main {
  margin: 0 auto;
  max-width: 960px;
}

section {
  padding: 15px;
}

.searchbox {
  display: block;
  width: 100%;
  padding: 15px;

  border: none;
  outline: none;
  background: none;

  background-color: #FFF;
  border-radius: 8px;

  color: #53565A;
  font-size: 20px;
  font-weight: 300;

  transition: 0.4s ease-out;
}

.searchbox:focus {
  box-shadow: 0px 0px 8px 3px #033e6e;
}

.errorMovie {
  font-size: 40px;
  text-align: center;
  color: #FFF;
}

.results {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px;
}

.results .resultForOne {
  width: 33.333%;
  min-width: 250px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.results .resultForOne img {
  display: block;
  width: 100%;
  height: 400px;
}

.results .resultForOne h3 {
  text-align: center;
  color: #FFF;
  font-size: 20px;
  padding: 10px;
  background: #445565;
  /* Pour que l'espace contenant les titres soit de la mÃªme tailles tous le temps : */
  flex: 1 1 100%; 
  transition: 0.4s ease-out;
}

.results .resultForOne:hover h3 {
  background: #FFF;
  color: #2C3E50;
}

.results .resultForOne .errorImg {
  color: #FFF;
  font-size: 28px;
  text-align: center;
} 

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #445565;
  overflow-y: scroll;
}

.popup .content {
  display: block;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 25px;

  color: #FFF;
}

.popup .content h2 {
  font-size: 28px;
  font-weight: 600;
}

.popup .content h2 span {
  font-weight: 300;
  font-size: 20px;
}

.popup .content .rating {
  margin-bottom: 30px;
}

.popup .content .plot {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px 30px;
}

.popup .content .plot img {
  flex: 1 1 50%;
  max-width: 300px;
  padding: 0 15px;
}

.popup .content .plot .details {
  flex: 1 1 50%;
  padding: 15px;
}

.popup .content .plot .details p {
  margin: 0px 0 18px 0;
}

.popup .content .close {
  display: inline-block;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: 700;

  background: #18BC9C;
  color: #2C3E50;
  border-radius: 8px;
  border: none;
  outline: none;
  appearance: none;

  cursor: pointer;
  transition: 0.4s ease-out;
}

.popup .content .close:hover {
  background-color: #1160af;
}

/* Media Queries */

@media screen and (max-width: 359px) {

  .App {
    width: 100%;
  }

  .searchbox {
    width: 90%;
    margin: 0 auto;
  }

  .results .resultForOne {
    width: 94%;
    margin: 0 auto;
  }

  .results .resultForOne img {
    height: 350px;
  }

  .popup {
    width: 100%;
    height: 105vh;
  }
}

@media screen and (min-width: 360px) and (max-width: 498px) { 

  .App {
    width: 100%;
  }
  
  .searchbox {
    width: 90%;
    margin: 0 auto;
  }

  .results .resultForOne {
    width: 90%;
    margin: 0 auto;
  }

  .popup {
    width: 100%;
    height: 105vh;
  }

}

@media screen and (min-width: 499px) and (max-width: 768px) {

  .results .resultForOne {
    width: 48%;
    margin: 0 auto;
  }

  .popup {
    width: 100%;
    height: 105vh;
  }

  .popup .content h2 {
    font-size: 42px;
  }

  .popup .content h2 span {
    font-size: 36px;
  }

  .popup .content .rating {
    font-size: 26px;
  }

  .popup .content .plot .details p {
    font-size: 26px;
  }

}